import { Hono } from 'hono';
import { successResponse, errorResponse } from '../utils/response';

const reports = new Hono();

// 获取成绩报告
reports.get('/scores', async (c) => {
  try {
    // 这里应该从数据库获取数据
    const data = {
      reportId: 'rpt_001',
      title: '期末考试成绩报告',
      generatedAt: new Date().toISOString(),
      summary: {
        totalStudents: 45,
        averageScore: 85.5,
        passRate: 93.3,
        excellentRate: 40.0
      },
      details: [
        { subject: '语文', average: 88.2, highest: 98, lowest: 72 },
        { subject: '数学', average: 85.5, highest: 100, lowest: 65 },
        { subject: '英语', average: 82.8, highest: 95, lowest: 68 }
      ]
    };
    
    return c.json(successResponse(data));
  } catch (error) {
    return c.json(errorResponse('获取报告失败'), 500);
  }
});

// 获取学生个人报告
reports.get('/student/:studentId', async (c) => {
  try {
    const studentId = c.req.param('studentId');
    
    // 这里应该从数据库获取数据
    const data = {
      studentId,
      name: '张三',
      reports: [
        {
          examId: 'exam_001',
          examName: '期中考试',
          totalScore: 285,
          rank: 5,
          subjects: [
            { name: '语文', score: 92, classAverage: 88.2 },
            { name: '数学', score: 95, classAverage: 85.5 },
            { name: '英语', score: 98, classAverage: 82.8 }
          ]
        }
      ]
    };
    
    return c.json(successResponse(data));
  } catch (error) {
    return c.json(errorResponse('获取学生报告失败'), 500);
  }
});

export default reports;